<template>
    <div class="detail-presell">
        <div v-if="list.status===1">
            <van-swipe class="swiper" :autoplay="0" :show-indicators=false indicator-color="white">
                <van-swipe-item><img class="top-img" :src="list.arrayImgUrl" alt=""></van-swipe-item>
            </van-swipe>
            <div class="presell-title">
                <span class="star" v-if="singer.singername">{{singer.singername}}</span>
                <span class="t">{{list.title}}</span>
            </div>
            <div class="presell-value">
                <span class="cost">{{list.realPrice}}积分</span>
                <!--<em class="tags">{{list.isRestriction}}</em>-->
                <em class="tags" v-if="list.isVip">会员专享</em>
                <i class="selled">库存 {{list.totalStock}} 件</i>
            </div>
            <p class="tips">截止时间：{{$formatTime(new Date(list.welfareEndTime))}}</p>
            <p class="blue-tips">未成年人请在监护人同意下使用平台服务</p>
            <div class="follow-sth" @click="$isApp">
                <div class="follow-head"
                     :style="{background:'url('+ userHomePage.headUrl+')center no-repeat','background-size':'cover'}">
                </div>
                <div class="follow-nickname">{{userHomePage.account}}</div>
                <div class="follow-btn" :class="{'gray-font':userHomePage.isConcern===1}">
                    {{userHomePage.isConcern===0?'+关注':'已关注'}}
                </div>
                <div class="fans-num">粉丝：{{userHomePage.fansNum}}</div>
            </div>
            <van-cell class="should-know" to="/noteWelfareExchange" title="福利兑换须知" is-link/>
            <div class="img-box">
                <img class="showImg" :src="item" v-for="item in list.imgUrl" alt="">
            </div>
            <div class="footer-btn">
                <van-button class="opreate-btn" :class="{grayColor:changeGray}" type="primary" size="large"
                            @click="$isApp()">{{buttonText}}
                </van-button>
            </div>
        </div>
        <div v-else-if="list.status===0"><img class="default-img" src="../../assets/images/no-goods.png" alt=""></div>
    </div>
</template>

<script>
  export default {
    name: "DetailWelfare",
    data() {
      return {
        changeGray: false,
        buttonText: '立即兑换',
        singer: {},
        userHomePage: {},
        list: {}
      }
    },
    mounted() {
      this.getData();
      setTimeout(() => {
        this.deadline();
      }, 500)
    },
    created() {
      document.title = '福利社详情';
    },
    methods: {
      deadline: function (t) {
        let date = new Date().getTime();
        if ((date > this.list.welfareEndTime) || this.list.totalStock === 0) {
          this.buttonText = '查看其他商品';
          this.changeGray = true;
        }
      },
      getData: function () {
        let params = this.$queryParams(location.href);
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/welfare/welfareDetails', // 福利享清
          data: this.$qs.stringify({
            userId: params.userId,
            id: params.id
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.list = res.data.data;
              this.singer = {...this.singer, singerName: res.data.data.singer};
              this.userHomePage = res.data.data.userHomePage;
              this.list.imgUrl = this.list.imgUrl.split(',');
              if (this.list.isRestriction === 0) {
                this.list.isRestriction = '不限购';
              } else {
                this.list.isRestriction = '限购';
              }
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
    .img-box {
        padding-bottom: 1rem;
        font-size: 0;
    }

    .default-img {
        width: 100%;
    }

    .grayColor {
        background: #c7c1c1 !important;
    }

    .top-img {
        width: 100%;
    }

    .gray-font {
        border: .02rem solid gray !important;
        color: gray !important;
    }

    .showImg {
        width: 100%;
    }

    .detail-presell {
        height: 100%;
        width: 100%;
        .swiper {
            /*height: 5.8rem;*/
            width: 100%;
        }
        .presell-title {
            color: #000;
            font-weight: 600;
            /*max-height: 1.1rem;*/
            line-height: .4rem;
            padding: .2rem;
            background: #fff;
            /*overflow: hidden;*/
            /*-ms-text-overflow: ellipsis;*/
            /*text-overflow: ellipsis;*/
            /*display: -webkit-box;*/
            /*-webkit-box-orient: vertical;*/
            /*-webkit-line-clamp: 2;*/
            .t {
                font-size: .28rem;
                vertical-align: middle;
            }
            .star {
                padding: 0 .08rem;
                background: #FF2E4D;
                text-align: center;
                font-size: .26rem;
                color: #fff;
                -webkit-border-radius: .14rem;
                -moz-border-radius: .14rem;
                border-radius: .14rem;
            }
        }
        .presell-value {
            width: 100%;
            height: .5rem;
            padding: 0 .2rem;
            background: #fff;
            .cost {
                height: 100%;
                color: #FF2E4D;
                font-size: .36rem;
                line-height: .5rem;
                display: block;
                float: left;
            }
            .tags {
                display: block;
                line-height: .32rem;
                height: .3rem;
                margin: .1rem .1rem 0 .1rem;
                -webkit-border-radius: .04rem;
                -moz-border-radius: .04rem;
                border-radius: .04rem;
                border: .01rem solid #909090;
                font-size: .2rem;
                color: #909090;
                float: left;
                padding: 0 .05rem;
            }
            .selled {
                color: #909090;
                font-size: .24rem;
                float: right;
                display: block;
                line-height: .55rem;
                height: 100%;
            }
        }
        .tips {
            width: 100%;
            height: .87rem;
            line-height: .3rem;
            padding: .27rem .2rem .3rem .2rem;
            font-size: .24rem;
            color: #909090;
            background: #fff;
        }
        .blue-tips {
            background: #fff;
            padding: 0 .2rem .4rem .2rem;
            font-size: .22rem;
            color: #458AFC;
        }
        .follow-sth {
            height: 1.37rem;
            width: 100%;
            padding: 0 .2rem;
            background: #fff;
            margin: .32rem 0;
            overflow: hidden;
            .follow-head {
                height: .71rem;
                width: .71rem;
                margin: .33rem 0;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                float: left;
            }
            .follow-nickname {
                height: 50%;
                line-height: 1rem;
                width: 5rem;
                float: left;
                font-size: .33rem;
                color: #333333;
                padding: 0 .15rem;
            }
            .fans-num {
                height: 50%;
                width: 5rem;
                line-height: .45rem;
                font-size: .22rem;
                color: #909090;
                padding: 0 .15rem;
                float: left;
            }
            .follow-btn {
                float: right;
                height: .56rem;
                width: 1rem;
                -webkit-border-radius: .1rem;
                -moz-border-radius: .1rem;
                border-radius: .1rem;
                border: .01rem solid #FF2E4D;
                color: #FF2E4D;
                line-height: .56rem;
                text-align: center;
                margin: .41rem 0;
            }
        }
        .should-know {
            color: #333333;
            font-size: .28rem;
        }
        .footer-btn {
            height: .98rem;
            width: 100%;
            background: #fff;
            margin: 0 auto;
            position: fixed;
            left: 0;
            bottom: 0;
            right: 0;
            border: 1px solid #F2F2F2;
            .opreate-btn {
                display: block;
                height: .7rem;
                border-radius: .35rem;
                line-height: .7rem;
                border: 0;
                font-size: .28rem;
                background: #FF2E4D;
                width: 5rem;
                margin: .14rem auto;
            }
        }
    }
</style>

